<!-- 详细视频 -->

<template>
    <div>
        <Xgplayer :config="config" @player="Player = $event" />
        <el-select v-model="videoIndex">
            <el-option v-for='(video, index) in videoform' :key="index" :label= "video.name" :value= index></el-option>
        </el-select>
        <el-button type="primary" @click="pushVideo()">跳转</el-button>
        <el-input v-model="form.text" placeholder="发表评论"></el-input>
        <el-button type="primary" @click="submit(0)">评论</el-button>
        <el-card class="box-card" v-for='(ListPiece, index) in List' :key="index">
            <div v-for='(ListOne, index) in ListPiece' :key="index">
                <div v-if="index == 0">
                    <h3>{{ ListOne.userName }} 评论: {{ ListOne.text }}</h3>
                    <el-popover placement="bottom" title="回复评论" width="200" trigger="click">
                        <el-button slot="reference">评论</el-button>
                        <el-input v-model="form.text" placeholder="发表评论"></el-input>
                        <el-button type="primary" @click="submit(ListOne.id)">评论</el-button>
                    </el-popover>
                </div>
                <span v-if="index != 0">{{ ListOne.userName }} 回复: {{ ListOne.text }}</span>

            </div>
        </el-card>
    </div>
</template>

<script>
import axios from 'axios';
import Xgplayer from 'xgplayer-vue';
const options = {
    inject: ['reload'],
    data() {
        return {
            config: {
                id: 'vs',
                url: this.$store.state.video.url,//修改为能播放的视频地址
                width: '100%',//宽度可以是 数字 ，也可以是百分比
                height: 600,
                playbackRate: [0.5, 0.75, 1, 1.5, 2],//倍速
            },
            Player: null,
            List: null,
            form: {
                text: "",
                date: new Date(),
                videoId: this.$store.state.video.id,
                userName: this.$store.state.name,
                seniorId: 0,
            },
            ban: true,
            commentShow: false,
            videoform: '',
            videoIndex: '',
        }

    },
    methods: {
        getVideoComment() {
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/comment/videoComment",
                method: 'post',
                data: this.$store.state.video.id,
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {

                    this.List = res.data;
                });
        },
        async submit(seniorId) {
            await axios.request({
                baseURL: "http://localhost:8090/",
                url: "/user/selectByName?name=" + this.$store.state.name,
                method: 'get',
                async: false,
                headers: { 'Content-Type': 'application/json' }
            })
                .then(res => {
                    var istrue = new Date(res.data.banday) < new Date;
                    this.ban = istrue;
                });
            if (this.ban) {
                this.form.seniorId = seniorId;
                axios.request({
                    baseURL: "http://localhost:8090/",
                    url: "/comment/addVcomment",
                    method: 'post',
                    data: this.form,
                    headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
                })
                    .then(res => {
                        alert("评论成功");
                        this.form.text = '';
                        this.reload();
                    });
            } else {
                alert("您已被禁言");
            }

        },
        getVideoXiLie(){
            axios.request({
                    baseURL: "http://localhost:8090/",
                    url: "/video/selectVideoXiLie",
                    method: 'post',
                    data: this.$store.state.video.collection,
                    headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
                })
                    .then(res => {
                        console.log("返回");
                        console.log(res.data);
                        this.videoform = res.data;
                    });
        },
        pushVideo(){
            if(this.$store.state.video.name != this.videoform[this.videoIndex].name){
                this.$store.commit('saveVideo', this.videoform[this.videoIndex]);
                this.reload();
            }else{
                alert("您已在此视频下")
            }
            
        }
    },
    components: {
        Xgplayer
    },
    mounted: function () {
        this.getVideoComment();
        this.getVideoXiLie();
    }
}

export default options;

</script>